header {
  background-color: $light-pink-color;
}
.container.header {
  position: relative;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-top: 6px;
  width: 100%;

  @media (min-width: 768px) {
    padding-top: 37px;
  }

  @media (min-width: 1200px) {
    padding-left: 5px;
    width: 1200px;
    margin-left: 40px;
    margin-right: 40px;
    max-width: 1200px;
  }
}

.logo {
  @extend %link;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: $main-red-color;
  font-family: Titan One;
  font-size: 26px;
  width: 200px;
  transition: 250ms;
  &:hover,
  &:focus {
    color: $main-red-color;
    text-decoration: none;
    transform: scale(1.1);
  }
}

.logo-img {
  width: 50px;
  height: 50px;
  margin-right: 5px;
  fill: currentColor;
  background-color: #fff;
  border-radius: 100%;
}

.menu-container {
  display: inline-flex;
  align-items: center;
  justify-content: space-between;
  height: 502px;
  width: inherit;
  padding-top: 60px;
  filter: drop-shadow(-8px 0px 20px rgba(0, 0, 0, 0.25));
  @media (max-width: 1199px) {
    position: fixed;
    top: 0;
    right: 0;
    width: 250px;
    background-color: $menu-background-color;
    display: none;
    &.is-open {
      display: block;
    }
  }
  @media (min-width: 1200px) {
    display: flex;
    flex-direction: row-reverse;
    height: 100%;
    padding-top: 0;
    margin-left: 110px;
  }
}

.menu-nav {
  color: $main-white-color;
  font-style: normal;
  font-weight: bold;
  font-size: 14px;
  line-height: 18px;
  & .menu {
    @media (min-width: 1200px) {
      display: flex;
      align-items: center;
      justify-content: center;
      & .menu-item:not(:last-child) {
        margin-right: 35px;
      }
    }
  }
}

.menu-item-link {
  display: block;
  height: 42px;
  padding-left: 21px;
  padding-top: 12px;
  padding-bottom: 12px;
  color: $main-white-color;
  border: 1px solid #e18298;
  &:hover,
  &:focus {
    color: $main-red-color;
    text-decoration: none;
  }
  @media (min-width: 1200px) {
    display: flex;
    align-items: center;

    font-size: 16px;
    border: none;
    padding: 0%;
  }
}

.menu-button {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 20px;
  height: 20px;
  padding: 0;
  margin: 0;
  border: none;
  outline: 0;
  color: $main-white-color;
  background-color: transparent;
  transition: 250ms;
  &:hover,
  &:focus {
    transform: scale(1.1);
    outline: 0;
  }
  @media (min-width: 1200px) {
    display: none;
  }
}

.menu-button-icon.close {
  display: none;
}
.menu-button.is-open {
  position: fixed;
  top: 23px;
  right: 34px;
  z-index: 5;
  & .menu-button-icon.close {
    display: block;
    fill: currentColor;
  }
}

.menu-button-icon.open {
  fill: currentColor;

  .menu-button.is-open & {
    display: none;
  }
}

.buy-now {
  width: 209px;
  height: 44px;
  margin-bottom: 32px;
  margin-left: 21px;
  font-weight: 700;
  transition: 250ms;
  &:hover,
  &:focus {
    transform: scale(1.1);
    outline: 0;
    background-color: #ecebeb;
  }
  @media (min-width: 1200px) {
    width: 160px;
  }
  @media (min-width: 1200px) {
    margin: 0;
  }
}

.buy-now.tablet {
  margin-bottom: 0;
  margin-left: auto;
  margin-right: 30px;
  width: 160px;
  .menu-container.is-open + & {
    display: none;
  }

  @media (max-width: 767px) {
    display: none;
  }
  @media (min-width: 1200px) {
    display: none;
  }
}

.buy-now-img {
  width: 6px;
  height: 8px;
  margin-left: 26px;
}

